<template>
  <div class="container">
    <button class="btn btn-primary" type="button">{{ msg }}</button>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Ping',
  data() {
    return {
      msg: '',
    };
  },
  methods: {
    getMessage() {
      // const path = 'http://localhost:5000/ping';
      const path = 'http://shitty-backend-shopfinder.herokuapp.com/ping';
      axios.get(path)
        .then((res) => {
          this.msg = res.data;
          console.log(res);
          console.log(res.request);
          console.log(res.headers);
        })
        .catch((error) => {
          // eslint-выключение следующей строки
          console.error(error);
        });
    },
  },
  created() {
    this.getMessage();
  },
};
</script>

